/* harmony default export */ __webpack_exports__["a"] = ({id:"1008",title:"Spookschip Eleonore",created:"1 juni 2018",location:"Fowey",geolocation:{lat:50.335878,lng:-4.634598},paragraphs:["Wanneer we op donderdagochtend wakker worden in de baai van Dartmouth, is een dikke\n    wolk mist het dal in gedaald. We verwachten dat op zee het zicht beter zal zijn en gooien de\n    trossen los. In eerste instantie blijkt dat ook het geval te zijn, maar hoe dichter we bij de\n    kaap Start Point komen, hoe dikker de mist wordt. Ons blikveld reikt niet verder dan een\n    meter of vijftien. We hebben vier middelen voor zo'n situatie: de radar, de AIS (automatisch\n    identificatie systeem), onze oren en onze ogen (eyeball navigation).","We weten dat er allerlei zeil- en vissersschepen in de buurt varen, maar die blijven volgens\n    de AIS op voldoende afstand. We zijn allebei 120% geconcentreerd om bijtijds te kunnen\n    bijsturen of afremmen.","Opeens doemt vanuit de dikke mist zeilschip Eleonore op dat ons rakelings met hoge\n    snelheid op 10 meter kruist. \"Ahoy\", is de droge groet van de schipper en weg is Eleonore\n    weer. Dit schip was zelfs op de radar onzichtbaar. Het heeft dus g\xE9\xE9n radarreflector, g\xE9\xE9n\n    massa van ijzer of staal en g\xE9\xE9n AIS. Maar wel met hoge snelheid door de mist varen. Hij had\n    ons met gemak midscheeps kunnen rammen. Al met al heeft het misschien 5 seconden\n    geduurd, maar de hartkloppingen zijn er bij ons niet minder om.","Twee uur later trekt de mist op en varen we op ons dooie akkertje naar Fowey."],photos:[{src:__WEBPACK_IMPORTED_MODULE_0__photos_1008_1_juni_de_mist_daalt_langzaam_de_baai_in_800px_jpg___default.a},{src:__WEBPACK_IMPORTED_MODULE_1__photos_1008_1_juni_ondanks_het_slechte_weer_besluiten_we_toch_uit_te_varen_800px_jpg___default.a,title:'Ondanks het slechte zicht besluiten we toch uit te varen.'}]});

