/* harmony default export */ __webpack_exports__["a"] = ({id:"1011",title:"Passanten",created:"16 juni 2018",location:"A CoruÃ±a",geolocation:{lat:43.38500000,lng:-8.60361111},paragraphs:["Vaak als we een oversteek maken, hebben we verstekelingen aan boord. Vogels, en we zijn dan verbaasd dat ze zo ver van het land over zee vliegen.","Op onze eerste reis naar Schotland in 2007 kregen we gezelschap van een klein vogeltje. Een soort musje dat ineengedoken op het voordek zat. Op een gegeven moment was het vogeltje gevlogen. Maar bij aankomst in Inverness bleek het dood in de ankerbak te liggen en hebben we haar begraven in de bosjes.","Tijdens een andere reis over de Doggerbank kwam er een wulp (herkenbaar aan de lange kromme snavel) aanvliegen die op onze banken neerstreek om bij te komen. Na een dag probeerde de wulp weer op eigen kracht verder te gaan, maar had daar overduidelijk moeite mee. Elke keer kwam de wulp weer terug naar de boot en landde dan weer op het dek. Totdat het niet meer lukte om aan boord te landen en ze in zee terecht kwam. We zagen vervolgens een grote zeemeeuw op de wulp duiken.....","Op de tweede dag van onze recente oversteek landde er een duif op onze boot. Die wilde heel graag bij ons binnen in de kajuit zitten. Dat vonden wij niet goed. Dus heeft de duif een hele dag en nacht in het gangboord gezeten. Bij de eerste zonnestralen de volgende ochtend is ze weggevlogen. Maar liet - tot onze stomme verbazing - een ei voor ons achter."],photos:[{src:__WEBPACK_IMPORTED_MODULE_0__photos_1011_14_juni_2018_duif_met_ei_800px_jpg___default.a}]});

