/* harmony default export */ __webpack_exports__["a"] = ({id:"1013",title:"Van MuxÃ­a naar Muros",created:"30 juni 2018",location:"Muros",geolocation:{lat:42.93583333,lng:-9.14388889},paragraphs:["Na A Coru\xF1a verkennen we op ons dooie gemak de Ria's van noord-Spanje. We varen korte stukken en blijven dan twee, drie nachten in een haven liggen of gaan voor anker. Toen we acht jaar geleden hier waren, hebben we de Ria's niet echt bekeken. Maar nu hebben we alle tijd van de wereld.","Via Laxe en Finisterre (niet gezien, want in de dikke mist) zijn we naar Mux\xEDa en  vervolgens naar Muros gevaren. Er zijn grote verschillen tussen beide stadjes. Mux\xEDa heeft een nieuwe haven waar elke avond ongeveer vijf boten binnenlopen. De meeste ligplaatsen zijn leeg. In de nieuwe haven van Muros ligt de passantensteiger bijna vol en hebben we uitzicht op het doen en laten van onze buren. Wat weer tot gezellige praatjes leidt.","Mux\xEDa is een stadje met veel betonbouw, een beetje armoedig, een paar wandelaars. De vrouwen verdienen wat bij met de verkoop van eigengemaakte kleding, tasjes, gebreide dekentjes. Muros heeft een middeleeuwse kern, en er zijn veel toeristen. En alles moet op slot: het hek van de haven, de toegang van het havengebouwtje, het toiletgebouw. Levendig is het wel.","Dit weekend is hier het grote San Pedro feest, compleet met live bands, kermis en roeiwedstrijden. De sloepen gaan bloedhard, worden geroeid door 12 mannen aangevuld met twee stuurmannen. De voorste stuurman steekt zijn roeiriem recht naar beneden in het water om een scherpe bocht om de boei te maken, en de achterste stuurman roeit bij. Hard werken voor de jongens....."],photos:[{src:__WEBPACK_IMPORTED_MODULE_0__photos_1013_30_juni_2018_kaap_finisterre_in_de_mist_800px_jpg___default.a,title:"Kaap Finisterre in de mist"},{src:__WEBPACK_IMPORTED_MODULE_1__photos_1013_30_juni_2018_huizen_in_muxia_800px_jpg___default.a,title:"Huizen in Mux\xEDa"},{src:__WEBPACK_IMPORTED_MODULE_2__photos_1013_30_juni_2018_muros_stamt_uit_de_10e_eeuw_800px_jpg___default.a,title:"Muros stamt uit de 10e eeuw"},{src:__WEBPACK_IMPORTED_MODULE_3__photos_1013_30_juni_2018_veel_bloemen_in_muros_800px_jpg___default.a,title:"Veel bloemen in Muros"},{src:__WEBPACK_IMPORTED_MODULE_4__photos_1013_30_juni_2018_slanken_sloepen_die_heel_hard_kunnen_800px_jpg___default.a,title:"Slanke sloepen die heel hard kunnen met 12 roeiers"},{src:__WEBPACK_IMPORTED_MODULE_5__photos_1013_30_juni_2018_met_een_bloedgang_om_de_boei_heen_800px_jpg___default.a,title:"Met een bloedgang om de boei heen"}]});

