/* harmony default export */ __webpack_exports__["a"] = ({id:"1014",title:"De bergplaatsen van Galicia",created:"8 juli 2018",location:"Riveira",geolocation:{lat:42.26861111,lng:-9.08555556},paragraphs:["In Nederland is het al wekenlang volop zomer. Hier niet. We liggen vijf dagen in Muros, omdat het weer herfstachtig is (bewolkt, regen en 170). Een goede gelegenheid om te wandelen. Tijdens onze wandelingen zien we overal stenen huisjes op palen staan. De meeste zijn oud en goed onderhouden, sommigen zijn in verval, maar soms zien we er ook een nieuw gebouwd. Ze staan op de meest onmogelijke plekken: langs steile straatjes en in kleine achtertuinen.","Eerst dachten we dat het grafplaatsen waren of kapelletjes. Maar het blijken opslagplaatsen. Men bewaarde voorraden graan in deze huisjes, en andere goederen om ze droog op te slaan. De huisjes staan op palen om te voorkomen dat er ratten, muizen of andere ongewenste beesten bij kunnen. Hele slimme constructies, klimaatneutraal.","Deze bergplaatsen (Horreos) zijn typisch voor Galici\xEB. En ze worden nog steeds gebruikt. We zien ze met kattenluikjes, met BBQ's en tuinstoelen. Het graan wordt tegenwoordig elders opgeslagen."],photos:[{src:__WEBPACK_IMPORTED_MODULE_0__photos_1014_8_juli_2018_berplaatsen_van_galicia_800px_jpg___default.a},{src:__WEBPACK_IMPORTED_MODULE_1__photos_1014_8_juli_2018_berplaatsen_van_galicia_2_800px_jpg___default.a}]});

