/* harmony default export */ __webpack_exports__["a"] = ({id:"1018",title:"Grote en kleine vissers (2)",created:"20 juli 2018",location:"Portonovo",geolocation:{lat:42.40305556,lng:-8.82472222},paragraphs:["De vangst van vissen en schelpen is een belangrijke bron van inkomsten in de Ria's van Spanje. Kleine open bootjes zijn aan het werk met laag water.","In zo'n klein bootje zitten twee vissers. Bij laag water schrapen zij de schelpen van de zeebodem met een soort RVS-korf. Die korf zit aan een lange steel, waardoor ze een groter werkgebied hebben van soms wel 20 meter. Als ze de korf binnenhalen, zitten er niet alleen schelpen in, maar hangen er ook planten aan. Lichamelijk zwaar werk. Als het laag tij voorbij is, varen ze naar de visafslag waar de schelpen verhandeld worden.","De bootjes zien er goed onderhouden uit. Het is serieuze business, maar of het een vetpot is?"],photos:[{src:__WEBPACK_IMPORTED_MODULE_0__photos_1018_20_juli_2018_schelpen_vissen_bij_laag_water_800px_jpg___default.a,title:"Schelpen vissen bij laag water."},{src:__WEBPACK_IMPORTED_MODULE_1__photos_1018_20_juli_2018_de_korf_wordt_over_de_bodem_getrokken_800px_jpg___default.a,title:"De korf wordt over de bodem getrokken."},{src:__WEBPACK_IMPORTED_MODULE_2__photos_1018_20_juli_2018_de_vangst_gaat_in_mandjes_die_op_de_visafslag_verhandeld_worden_800px_jpg___default.a,title:"De vangst gaat in mandjes die op de visafslag verhandeld worden."},{src:__WEBPACK_IMPORTED_MODULE_3__photos_1018_20_juli_2018_bij_hoog_water_ligt_de_haven_weer_vol_de_korven_klaar_voor_de_volgende_dag_800px_jpg___default.a,title:"Bij hoog water ligt de haven weer vol; de korven klaar voor de volgende dag."},{src:__WEBPACK_IMPORTED_MODULE_4__photos_1018_20_juli_2018_ook_bewoners_zoeken_schelpen_op_het_strand_800px_jpg___default.a,title:"Ook bewoners zoeken schelpen op het strand."}]});

